body {
  background-color: #455a64       !important;
}

.card {
  margin: 0 auto;
  background-color: #4fc3f7;
}

.container-card {
  margin: 0 auto;
  width: 900px;
  /* height: 550px; */
  background-color: white;
}

.container {
  /* background-color: gray; */
  margin: 0 auto;
  width: 750px;
  background-color: white;
  padding-bottom: 20px;
}

h1 {
  text-align: center;
  font-size: 2em;
  margin-top: 5px;
  margin-bottom: 5px;
}

.number {
  color: gray;
}

.two-column-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 750px;
}

.two-column-container > div {
  -ms-flex: 1 1;
      flex: 1 1;
}

.info-container {
  -ms-flex-align: start;
      align-items: flex-start;
  padding: 10px;
  margin: 10px;
}

.info-subcontainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.info-subcontainer > div {
  -ms-flex: 1 1;
      flex: 1 1;
}

.info { 
  -ms-flex-align: start; 
      align-items: flex-start;
  padding: 10px;
}

.info p {
  color: white;
  margin-bottom: 5px;
}

.subtitle {
  font-weight: bolder;
}

.list p {
  margin-top: 2px;
  margin-bottom: 5px;
  padding-left: 5px;
}

.column-types {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.column-types > a {
  -ms-flex: 1 1;
      flex: 1 1;
}

.image-container {
  margin: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: white !important;
}

.image-container img {
  width: 300px;  
  display: block;
  margin: 0 auto;  
}

.input-container {
  margin: 20px;
  text-align: center;
}

.input-container .card {
  max-width: 500px;
  background-color: white;
}

.input-container label {
  display: inline-block;
  margin: 0 10px;
}

.input-container input {  
  width: 50%;
  display: inline-block;
  margin: 0 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.button-container {
  margin: 20px;
  padding: 20px;
  text-align: center;
  margin-bottom: 10px;
  padding-bottom: 10px;
}

.info-types .button-container {
  margin: 5px;
  padding: 5px;
}

.info-types p {
  font-weight: bolder;
  margin-bottom: 0px;
}

.info-types a {
  font-size: 1.2em !important;
}


a {
  color: #428BCA;
  text-decoration: none;
  font-size: 1.5em !important;
}

a:hover, a:focus {
  color: white !important;
  text-decoration: none !important;
}

label {
  font-size: 1.5em;
  color: #1565c0;
  font-family: Courier New, Courier, monospace;
  font-weight: bold;
}



